
import { TrendFilters } from "@/components/dashboard/TrendFilters"
import { VideoCard } from "@/components/dashboard/VideoCard"
import { supabase } from "@/lib/supabase"

// Disable caching for now to see updates
export const revalidate = 0;

export default async function Home() {
  // Fetch videos with their latest metrics
  const { data: videos, error } = await supabase
    .from('videos')
    .select(`
      *,
      daily_metrics (
        view_count,
        like_count,
        comment_count
      )
    `)
    .limit(20);

  if (error) {
    console.error("Error fetching videos:", error);
  }

  // Transform data for display
  const displayVideos = videos?.map((video: any) => {
    // Get latest metric (should only be one for now)
    const metric = video.daily_metrics?.[0] || { view_count: 0, like_count: 0, comment_count: 0 };

    return {
      id: video.id,
      title: video.title,
      thumbnailUrl: video.thumbnail_url || '',
      views: metric.view_count,
      likes: metric.like_count,
      comments: metric.comment_count,
      growthRate: 0 // We need historical data to calc growth. 0 for now.
    };
  }) || [];

  return (
    <div className="space-y-8">
      <header className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
          <p className="text-muted-foreground mt-1">
            Track the fastest growing YouTube Shorts in Japan.
          </p>
        </div>
        <div className="flex items-center space-x-2">
          {/* User profile or other actions could go here */}
        </div>
      </header>

      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h3 className="text-xl font-semibold">Top Trending Now</h3>
          <TrendFilters />
        </div>

        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
          {displayVideos.length > 0 ? (
            displayVideos.map((video) => (
              <VideoCard
                key={video.id}
                title={video.title}
                thumbnailUrl={video.thumbnailUrl}
                views={video.views}
                likes={video.likes}
                comments={video.comments}
                growthRate={video.growthRate}
              />
            ))
          ) : (
            <p className="col-span-4 text-center text-muted-foreground">
              No data available. Try running the ingestion API.
            </p>
          )}
        </div>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-7">
        <div className="col-span-4 rounded-xl border bg-card text-card-foreground shadow p-6">
          <h3 className="font-semibold mb-4">Growth Analytics</h3>
          <div className="h-[200px] flex items-center justify-center border-2 border-dashed rounded-lg text-muted-foreground">
            Need more data over time to show chart
          </div>
        </div>
        <div className="col-span-3 rounded-xl border bg-card text-card-foreground shadow p-6">
          <h3 className="font-semibold mb-4">Recent Activity</h3>
          <div className="space-y-4">
            {displayVideos.slice(0, 3).map((v) => (
              <div key={v.id} className="flex items-center gap-4">
                <div className="h-10 w-10 overflow-hidden rounded-full bg-muted">
                  {/* eslint-disable-next-line @next/next/no-img-element */}
                  <img src={v.thumbnailUrl} alt="thumb" className="h-full w-full object-cover" />
                </div>
                <div>
                  <p className="text-sm font-medium line-clamp-1">{v.title}</p>
                  <p className="text-xs text-muted-foreground">Updated just now</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
